var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 mb-5"},[_vm._m(0),_vm._m(1),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("El comportamiento del mercado frente a un producto suele obedecer a diferentes causas, la mayoría de ellas escapan del alcance de la empresa. No obstante, uno de los elementos sobre los que existe un margen de maniobra y que tiene incidencia directa sobre el mercado es la determinación del precio.")]),_c('Separador'),_vm._m(2),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Con esta se presume que el precio se establecerá por la unión de dos variables fundamentales: la necesidad de los consumidores y la oferta de los productores. ")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("La ley de la demanda y de la oferta se unen para dar cabida a esta teoría; la primera apunta a una relación inversa, donde a menor sea el precio de un bien o servicio, mayor será la demanda, es decir, la intención de los consumidores de adquirirlo. Por otra parte, la ley de la oferta describe una relación directa, donde a mayor precio del producto, mayor será la intención de los productores de ofrecerlo.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Lo que se busca al estudiar el mercado es buscar ese equilibrio donde el precio es lo suficientemente bajo para que los consumidores lo quieran, pero no tanto para que el costo de producción sea superior al de venta. Por ello, se insiste tanto en políticas de mejoramiento continuo de la producción, porque permiten hacer más eficiente la transformación de bienes, reduciendo su costo de fabricación y, por lo tanto, dando un mayor margen para reducir el precio de venta, generando así una fuerte ventaja competitiva frente a terceros en mercados competitivos.")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Se suele decir que el mercado tiene la capacidad de autorregularse bajo el modelo económico actual, esta afirmación proviene de la teoría, puesto que aquí se explica cómo el precio de los productos fluctúa de tal forma que si sube demasiado, los clientes no lo comprarán, por lo que el exceso de oferta para la baja demanda obligará a los productores a reducir el precio de venta lo suficiente como para salir de su inventario. Aquel punto en el que coinciden las pretensiones de la oferta con las de la demanda, es conocido como el equilibrio del mercado.")]),_vm._m(3),_c('Separador'),_vm._m(4),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("La demanda de un producto es equivalente a la cantidad de compradores multiplicada por la cantidad de productos que adquiere cada uno de ellos en un periodo de tiempo determinado. Aunque a primera vista parece un cálculo sencillo, esconde muchos elementos objeto de estudio detrás. Por ejemplo, ¿Cómo determino el número de compradores que tendrá mi producto? O, ¿Cuántas unidades adquiere cada uno y con qué frecuencia?")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("A continuación, se describen los factores que se deben tener en cuenta para calcular la demanda:")]),_c('div',{staticClass:"tarjeta tarjeta--azul2 p-4 mb-5"},[_c('SlyderA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Segmentar el mercado")]),_c('br'),_c('p',[_vm._v("Los productos suelen estar enfocados a una parte de la población en concreto, pueden existir nichos más o menos grandes dependiendo del tipo de producto, pero una correcta segmentación permite descartar una cantidad significativa de personas que no cumplen con las condiciones o características para comprar el producto.")]),_c('p',[_vm._v("Por ejemplo, si las dimensiones de la empresa no son muy significativas, el primer filtro lógico es seleccionar el municipio o la localidad donde se encuentra la fábrica; posteriormente, podría fraccionarse de acuerdo con la naturaleza del bien producido, si se trata de una empresa que vende maquillaje, la siguiente segmentación que puede hacerse es la de separar hombres y mujeres. ")])]),_c('div',{staticClass:"col-md-5 px-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-46.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Definir mercado meta")]),_c('br'),_c('p',[_vm._v("Una vez determinada la población objetivo, es necesario definir el mercado meta, es decir, aquella porción del segmento al que se le espera vender el producto. Siguiendo con el ejemplo anterior, se podrían descartar las mujeres menores de 15 años y mayores de 70, así el mercado meta estaría compuesto por mujeres entre 16 y 69 años; se puede ser más específico si se considera el rango de precios del producto y así descartar, por ejemplo, las mujeres que pertenecen a los estratos 1 y 2.")])]),_c('div',{staticClass:"col-md-5 px-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-47.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Análisis de la competencia")]),_c('br'),_c('p',[_vm._v("Salvo que el producto en mención sea completamente nuevo, puede estudiarse su comportamiento en el mercado con la venta de los competidores. Cuántas unidades se han vendido, cuáles son los precios promedio y las inconformidades que tienen sus clientes frecuentes. Es viable aplicar técnicas de recolección de información como: las entrevistas, o apegarse de información elaborada por otras fuentes como: las cámaras de comercio y otras entidades estatales.")])]),_c('div',{staticClass:"col-md-5 px-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-48.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Estimar porción de mercado que se espera cubrir")]),_c('br'),_c('p',[_vm._v("La inclusión de un producto en un mercado implica competir con otros proveedores de productos similares o sustitutos. Salvo en situaciones de monopolio, es prácticamente imposible lograr abarcar toda la demanda de un bien. Esta fase es la más subjetiva y con mayor margen de error, se trata de determinar qué cantidad del mercado podrá ocupar el bien producido.")]),_c('p',[_vm._v("Por ejemplo, si se venden mensualmente 1.000 kits de maquillaje en la población designada a las mujeres en el rango de edad y en la estratificación definida, la empresa puede aspirar a participar en el mercado con una venta de 100 de esos kits con similares características, en este caso estimaría una participación del 10 %. Por otro lado, muchos factores pueden afectar en este aspecto, tales como: la cantidad de competidores, el tamaño de la demanda y la capacidad de producción, entre otros.")])]),_c('div',{staticClass:"col-md-5 px-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-49.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Calcular la demanda")]),_c('br'),_c('p',[_vm._v("Habiendo definido el número de unidades que se esperan vender en un periodo determinado, en este caso mensual, solo resta multiplicarlo por el precio de venta. Muchos factores intervienen en la determinación del precio; tales como: el caso del costo de producción, la política de entrada al mercado y la diferenciación o ventaja competitiva.")]),_c('p',[_vm._v("Por otro lado, algunas empresas compiten con un menor precio de venta y otros con productos de mayor calidad, por lo que, sea cual sea la estrategia, debe existir un margen de rentabilidad que permita cubrir los gastos de ventas y administrativos, lo que dejar utilidad suficiente para garantizar la permanencia en el mercado. ")])]),_c('div',{staticClass:"col-md-5 px-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img-51.png"),"alt":"Texto que describa la imagen"}})])])])])],1),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Finalmente, es indispensable conocer qué se puede hacer de manera permanente para alcanzar el objetivo de producción y el porcentaje de ventas al final del proceso productivo.")])],1)],1)}
